// ** React Imports
import { Link } from 'react-router-dom'

// ** Custom Components

// ** Store & Actions
import { store } from '@store/store'

// ** Icons Imports
import { Eye } from 'react-feather'

// ** Reactstrap Imports
import { Badge } from 'reactstrap'
import { NavLink } from 'react-router-dom/cjs/react-router-dom.min'

// ** Renders Role Columns
const statusObj = {
  pending: 'light-warning',
  confirmed: 'light-success',
  cancelled: "light-danger",
  packed: 'light-secondary'
}

export const columns = [
  {
    name: 'Order Number',
    sortable: true,
    minWidth: '250px',
    sortField: 'Name',
    selector: row => row._id,
    cell: row => (
      <div className='d-flex justify-content-left align-items-center'>
        {/* {renderClient(row)} */}
        <div className='d-flex flex-column'>
          <Link
            to={`/view-order/${row._id}`}
            className='user_name text-truncate text-body' 
          >
            <span className='fw-bolder'>{row._id}</span>
          </Link>
          
        </div>
      </div>
    )
  },
  {
    name: 'Product',
    sortable: true,
    minWidth: '200px',
    sortField: 'role',
    selector: row => row.productId,
    cell: row => (
      store.getState().products.products.map(product => {
        if (product._id === row.productId) {
          return <span className='text-capitalize' key={row.productId}>{product.name}</span>
        }
      })
    )
  },
  {
    name: 'Customer Name',
    minWidth: '200px',
    sortable: true,
    sortField: 'billing',
    selector: row => row.customerId,
    cell: row => (
      store.getState().customers.customers.map(customer => {
        if (customer._id === row.customerId) {
          return <span className='text-capitalize' key={row.customerId}>{customer.name}</span>
        }
      })
    )
  },
  {
    name: 'Quantity',
    minWidth: '150px',
    sortable: true,
    sortField: 'billing',
    selector: row => row.quantity,
    cell: row => <span className='fw-bolder'>{row.quantity}</span>
  },
  {
    name: 'Status',
    minWidth: '138px',
    sortable: true,
    sortField: 'status',
    selector: row => row.status,
    cell: row => (
      <Badge className='text-capitalize' color={statusObj[row.status.toLowerCase()]} pill>
        {row.status}
      </Badge>
    )
  },
  {
    name: 'Actions',
    minWidth: '100px',
    cell: row => (
      <div className='column-action'>
        <NavLink 
              tag={Link}
              className='w-100'
              to={`/view-order/${row._id}`}
            >
              <Eye size={14} className='me-50' />
              
          </NavLink>
      </div>
    )
  }
]
