// ** User List Component
import Table from './Table'

// ** Reactstrap Imports
import { Row, Col } from 'reactstrap'

// ** Custom Components
import StatsHorizontal from '@components/widgets/stats/StatsHorizontal'

// ** Icons Imports
import { Box } from 'react-feather'

// ** Styles
import '@styles/react/apps/app-users.scss'
import { useSelector } from 'react-redux'

const UsersList = () => {
  const store = useSelector(state => state.orders)
  const orders = store.orders
  const confirmed = orders.filter(order => order.status.toLowerCase() === "confirmed")
  const pending = orders.filter(order => order.status.toLowerCase() === "pending")
  const cancelled = orders.filter(order => order.status.toLowerCase() === "cancelled")
  
  return (
    <div className='app-user-list'>
      <Row>
      <Col lg='3' sm='6'>
          <StatsHorizontal
            color='primary'
            statTitle='Total Orders'
            icon={<Box size={20} />}
            renderStats={<h3 className='fw-bolder mb-75'>{orders.length}</h3>}
          />
        </Col>
      <Col lg='3' sm='6'>
          <StatsHorizontal
            color='success'
            statTitle='Confirmed Orders'
            icon={<Box size={20} />}
            renderStats={<h3 className='fw-bolder mb-75'>{confirmed.length}</h3>}
          />
        </Col>
      <Col lg='3' sm='6'>
          <StatsHorizontal
            color='warning'
            statTitle='Pending Orders'
            icon={<Box size={20} />}
            renderStats={<h3 className='fw-bolder mb-75'>{pending.length}</h3>}
          />
        </Col>
      <Col lg='3' sm='6'>
          <StatsHorizontal
            color='danger'
            statTitle='Cancelled Orders'
            icon={<Box size={20} />}
            renderStats={<h3 className='fw-bolder mb-75'>{cancelled.length}</h3>}
          />
        </Col>
      </Row>
      <Table />
    </div>
  )
}

export default UsersList
